.projects-section {
  margin: 1rem;
  display: grid;
  justify-content: center;
}

.project {
  width: 95vw;
  height: 60vh;
  margin: 1rem 0.5rem;
  color: var(--light-one);
  padding-left: 2rem;
  padding-top: 3rem;
  overflow-y: hidden;
}

.project-1 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/port1.jpg") center/cover no-repeat;
}

.project-2 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/port2.jpg") center/cover no-repeat;
}

.project-3 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/port3.jpg") center/cover no-repeat;
}

.project-4 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/port4.jpg") center/cover no-repeat;
}

.project-5 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/port5.jpg") center/cover no-repeat;
}

.project-6 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/tenor_1.gif") center/cover no-repeat;
}

.project-7 {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/projects/port7.jpg") center/cover no-repeat;
}

.project-stack {
  color: var(--dark-two);
  text-transform: uppercase;
  font-family: var(--ff-teko);
  font-size: 2rem;
}

.project-title {
  text-transform: uppercase;
  line-height: 4rem;
  margin: 3rem 0;
  margin-top: 4rem;
  font-size: 5rem;
  letter-spacing: 0.1rem;
  font-family: var(--ff-teko);
}

.view-site {
  margin-top: 3rem;
  width: 15rem;
  text-align: center;
}

.more-folio {
  display: flex;
  justify-content: center;
}

@media (max-width: 700px) {
  .project-title {
    font-size: 4.5rem;
    margin-top: 4rem;
    margin-bottom: 3rem;
  }
}
