.education {
  overflow: hidden;
}

.education-section {
  display: grid;
  grid-template-columns: 100px 1fr;
  column-gap: 1rem;
  justify-content: center;
}

.college-title,
.school-title,
.year {
  color: var(--light-one);
  letter-spacing: 0.1rem;
  font-size: 1.5rem;
  margin-top: 2rem;
  padding: 1rem;
  height: auto;
  text-align: center;
  display: grid;
  place-items: center;
}

.year {
  transition: 0.3s;
}

.edu-btn {
  text-align: center;
  width: 100%;
  height: 100%;
  border: 2px solid var(--main-black);
}

.college-title {
  background: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.8)),
    url("../../assets/img/college.jpg") center/cover no-repeat;
}

.school-title {
  background: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.8)),
    url("../../assets/img/school.jfif") center/cover no-repeat;
}

.education img {
  max-width: 600px;
}

.certification-title {
  font-size: 2rem;
}

.education .text {
  margin-top: 1rem;
}

.skills {
  margin: 1.5rem 0 2.5rem 0;
}

.skill {
  margin: 1rem 0;
}

.skill-title {
  color: #555;
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.skill-bar {
  width: 70%;
  height: 8px;
  border-radius: 4px;
  background-color: #ece6fa;
  position: relative;
  overflow: hidden;
}

.skill-progress {
  position: absolute;
  height: 100%;
  width: 0%;
  top: 0;
  left: 0;
  background-color: var(--main-color);
  transition: 1s;
}

.education .column-1 {
  position: relative;
}

.education .column-1:before {
  content: "About";
  position: absolute;
  font-size: 8rem;
  font-weight: 800;
  color: var(--main-color);
  opacity: 0.04;
  top: 20px;
  left: 0;
  line-height: 0;
}

.education .points {
  width: 300px;
  top: 65%;
  left: 80%;
  opacity: 0.1;
}

.certificates {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
}

.course {
  display: block;
  color: var(--light-one);
  text-align: center;
  margin: 2rem;
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  place-items: center;
  border-radius: 10%;
  transition: 0.3s;
}

.course:hover {
  border: var(--hover-orange) solid 0.2rem;
}

.java {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("../../assets/img/java.png") center/cover no-repeat;
}

.javascript {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("../../assets/img/javascript.jpg") center/cover no-repeat;
}

.docker {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("../../assets/img/docker_kubernetes.png") center/cover no-repeat;
}

.graph {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("../../assets/img/graph.jfif") center/cover no-repeat;
}

.html-css {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)),
    url("../../assets/img/html-css.png") center/cover no-repeat;
}

.certificate-btn {
  font-size: 1rem;
  padding: 0.5rem 4rem;
  letter-spacing: 0.2rem;
  margin: 1rem;
  font-weight: bold;
}

.learning-sources {
  display: grid;
  gap: 2rem;
  color: var(--light-one);
}

.learning-source {
  border-radius: 50%;
}

@media (max-width: 900px) {
  .certificates {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 700px) {
  .year {
    transform: rotate(-90deg);
  }
  .education-section {
    column-gap: 0.001rem;
  }
  .college-title,
  .school-title,
  .year {
    font-size: 1rem;
  }
  .certification-title {
    font-size: 1.5rem;
  }
  .certificates {
    grid-template-columns: 1fr;
  }
  .course {
    font-size: 0.9rem;
  }
  .certificate-btn {
    padding: 0.1rem 2rem;
  }
}
