.about {
  overflow: hidden;
}

.about-me {
  background: var(--dark-one);
  height: 100%;
  padding: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
}

.about-para-title {
  margin-top: 1rem;
  color: var(--dark-two);
  font-family: var(--ff-teko);
  font-size: 2rem;
  letter-spacing: 0.1rem;
}

.about-text {
  color: var(--light-three);
  font-size: 0.8rem;
  letter-spacing: 0.01rem;
  line-height: 0.5rem;
  font-style: italic;
  text-align: center;
  margin-top: 45rem;
  line-height: 1rem;
}

.about-para {
  margin-top: 1.2rem;
  color: var(--light-one);
  letter-spacing: 0.1rem;
  width: 20rem;
  line-height: 1.7rem;
}

.about-img {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/about-img.jpg") center/cover no-repeat;
  padding-top: 7rem;
  margin: 2.5rem;
}

.notebook-title {
  color: var(--dark-two);
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 200;
  margin-top: 1rem;
}

.notebook {
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;
  margin-top: 2rem;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/notebook.PNG") center/cover no-repeat;
}

.about .text {
  margin-top: 1rem;
}

.skills {
  margin: 1.5rem 0 2.5rem 0;
}

.skill {
  margin: 1rem 0;
}

.skill-title {
  color: #555;
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.skill-bar {
  width: 70%;
  height: 8px;
  border-radius: 4px;
  background-color: #ece6fa;
  position: relative;
  overflow: hidden;
}

.skill-progress {
  position: absolute;
  height: 100%;
  width: 0%;
  top: 0;
  left: 0;
  background-color: var(--main-color);
  transition: 1s;
}

.about .column-1 {
  position: relative;
}

.about .column-1:before {
  content: "About";
  position: absolute;
  font-size: 8rem;
  font-weight: 800;
  color: var(--dark-two);
  opacity: 0.04;
  top: 20px;
  left: 0;
  line-height: 0;
}

.about .points {
  width: 300px;
  top: 65%;
  left: 80%;
  opacity: 0.1;
}

.capabilities-section {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
}

.tech {
  color: var(--light-three);
  letter-spacing: 0.1rem;
  line-height: 1rem;
  margin: 1rem;
}

.check-out-btn {
  margin: 1.5rem auto;
}

@media (max-width: 850px) {
  .about-me {
    grid-template-columns: 1fr;
    height: 100%;
  }
}
