.contact {
  height: 80vh;
}

.contact-info {
  color: var(--light-one);
  display: grid;
  column-gap: 0.01rem;
  margin: 1rem;
  align-items: center;
  gap: 0.2rem;
  grid-template-columns: 100px auto;
}

.email {
  font-size: 1rem;
}

.icon {
  font-size: 3rem;
}

@media (max-width: 850px) {
  .email {
    font-size: 0.8rem;
  }
}
