.footer {
  display: grid;
  justify-content: center;
  margin-top: 2rem;
  padding-top: 2rem;
  background: var(--dark-one);
}

.text {
  text-align: center;
  margin-bottom: 0.25rem;
}

.text span {
  font-size: 1rem;
  color: var(--light-one);
  transition: 0.3s;
}

.text span:hover {
  color: var(--hover-orange);
  cursor: none;
}
